.app-wrapper {
  padding-top: 10px;
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-areas:
    "h h "
    "n c";
  grid-gap: 10px;
  grid-template-rows: 120px 2fr;
  grid-template-columns: 3fr 10fr;
}

.theme-color {
  position: absolute;
  margin-left: -120px;
}

.app-wrapper-content {
  grid-area: c;
  background-color: white;
  position: relative;
  max-width: 920px;

}

.black {
  background-color: black;
}


.darkTheme {
  transition: all 1s;
  background:rgba(0, 0, 0, 0.833);
}
.whiteTheme {
  transition: all 1s;
  background:rgba(white, 0, 0, 0.833);
}